'use client'

import type React from "react"
import { useState, useEffect } from "react"
import { PanelLeft } from "lucide-react"

import { ChatInput, MessageList, Message } from "@/components/chat"
import SettingsMenu from "@/components/settings_menu"
import { Sidebar } from "@/components/Sidebar"

type ChatSession = {
  id: string
  name: string
  messages: Message[]
}

export default function Chat() {
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [useWebSearch, setUseWebSearch] = useState(false)
  const [chats, setChats] = useState<ChatSession[]>([])
  const [activeChatId, setActiveChatId] = useState<string | null>(null)
  const [sidebarOpen, setSidebarOpen] = useState(true)

  const activeChat = chats.find((c) => c.id === activeChatId)
  const messages = activeChat?.messages ?? []

function createNewChat() {
    const newChat: ChatSession = {
        id: crypto.randomUUID(),
        name: `New Chat ${chats.length + 1}`,
        messages: [],
    }
    setChats((prev) => [...prev, newChat])
    setActiveChatId(newChat.id)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!input.trim()) return

    const userMessage: Message = { role: "user", content: input }

  setChats((prevChats) =>
    prevChats.map((chat) =>
      chat.id === activeChatId
        ? {
            ...chat,
            messages: [...chat.messages, { role: "user", content: input }],
          }
        : chat
    )
  )

  setInput("")
  setIsLoading(true)


  try {
    const response = await fetch("http://localhost:8000/query", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ 
        query: userMessage.content, 
        history: messages, 
        use_web_search: useWebSearch 
      }),
    })
    if (!response.ok) throw new Error("Failed to get response")
    const data = await response.json()

    setChats((prevChats) =>
      prevChats.map((chat) =>
        chat.id === activeChatId
          ? {
              ...chat,
              messages: [...chat.messages, { role: "assistant", content: data.response }],
            }
          : chat
      )
    )
  } catch (err) {
    console.error("Error:", err)
    setChats((prevChats) =>
      prevChats.map((chat) =>
        chat.id === activeChatId
          ? {
              ...chat,
              messages: [...chat.messages, { role: "assistant", content: "Sorry, there was an error processing your request." }],
            }
          : chat
        )
      )
    } finally {
      setIsLoading(false)
    }
  }

  const isEmpty = messages.length === 0
  useEffect(() => {
    if (chats.length === 0) {
      const defaultChat: ChatSession = {
        id: crypto.randomUUID(),
        name: "New Chat 1",
        messages: [],
      }
      setChats([defaultChat])
      setActiveChatId(defaultChat.id)
    }
  }, [chats])

  return (
    <div className="bg-white dark:bg-neutral-900 font-sans h-screen overflow-hidden">
      <SettingsMenu/>
      <button
        className={`z-2 absolute top-6 left-6 py-2 px-3 rounded-full flex items-center gap-2 text-neutral-700 dark:text-neutral-300 font-semibold
        ${sidebarOpen 
          ? `bg-neutral-200 dark:bg-neutral-700 hover:bg-neutral-300 dark:hover:bg-neutral-600`
          : `bg-neutral-100 dark:bg-neutral-800 hover:bg-neutral-200 dark:hover:bg-neutral-700`
        }`}
        onClick={() => setSidebarOpen(!sidebarOpen)}
      >
        <PanelLeft size={20} />
        <span> {sidebarOpen ? "Close Sidebar" : "Open Sidebar" } </span>
      </button>

      <div className="flex h-screen">
        <Sidebar
          chats={chats}
          sidebarOpen={sidebarOpen}
          activeChatId={activeChatId}
          setActiveChatId={setActiveChatId}
          createNewChat={createNewChat}
        />
        <div className={`w-full p-4 h-screen flex flex-col items-center ${isEmpty ? "justify-center" : ""}`}>
            {isEmpty && (
              <div className="text-center py-6 ">
                <p className="font-bold text-neutral-700 dark:text-neutral-300 text-3xl">
                  What can I help you with?
                </p>
              </div>
            )}

            <MessageList messages={messages} isLoading={isLoading} />
            <ChatInput
              input={input}
              setInput={setInput}
              isLoading={isLoading}
              useWebSearch={useWebSearch}
              setUseWebSearch={setUseWebSearch}
              onSubmit={handleSubmit}
            />
        </div>
      </div>
    </div>
  )
}
