import { Plus } from "lucide-react"
import type { Message } from "@/components/chat"


type SidebarProps = {
  chats: { id: string; name: string; messages: Message[] }[]
  sidebarOpen: boolean
  activeChatId: string | null
  setActiveChatId: (id: string) => void
  createNewChat: () => void
}

export function Sidebar({
  chats,
  sidebarOpen,
  activeChatId,
  setActiveChatId,
  createNewChat
}: SidebarProps) {
  return (
    <div className={`
      h-screen 
      border-r border-neutral-100 dark:border-neutral-800 
      transition-all duration-200
      py-18
      ${sidebarOpen ? "translate-x-0 w-64 px-6" : "-translate-x-64 w-0"} 
    `}>
      {sidebarOpen && (
        <>
          <button
            onClick={createNewChat}
            className="flex items-center gap-2 justify-between px-3 py-2 mb-5 rounded-3xl
                      hover:bg-neutral-100 hover:dark:bg-neutral-800 
                      text-sm text-neutral-700 dark:text-neutral-300"
          >
            <Plus size={20} />
            <span className="font-semibold text-neutral-700 dark:text-neutral-300">New Chat</span>
          </button>

          <div className="overflow-y-auto h-[calc(100vh-150px)] pr-1">
            <ul className="space-y-2">
              {chats.map((chat) => (
                <li key={chat.id}>
                  <button
                    onClick={() => setActiveChatId(chat.id)}
                    className={`px-3 py-2 w-full text-left rounded-3xl ${
                      chat.id === activeChatId
                        ? `bg-neutral-100 dark:bg-neutral-800 hover:bg-neutral-200 dark:hover:bg-neutral-700
                           font-semibold text-neutral-700 dark:text-neutral-300`
                        : `hover:bg-neutral-100 hover:dark:bg-neutral-800
                           font-medium text-neutral-500 dark:text-neutral-400`
                    }`}
                  >
                    {chat.name}
                  </button>
                </li>
              ))}
            </ul>
          </div>
        </>
      )}
    </div>
  )
}