"use client"
import { useState, useEffect } from "react"
import { Settings2 } from "lucide-react"
import Dropdown from "@/components/Dropdown"

export default function SettingsMenu() {
  const [isOpen, setIsOpen] = useState(false)
  const [temperature, setTemperature] = useState(0.64516)
  const [model, setModel] = useState("mistralai/Mistral-7B-Instruct-v0.1")
  const [llmRerank, setLlmRerank] = useState(true)
  const [tone, setTone] = useState("neutral")

const temperatureStops = [
  { value: 0.1129, label: "Precise" },
  { value: 0.29032, label: "Focused" },
  { value: 0.46774, label: "Balanced" },
  { value: 0.64516, label: "Conversational" },
  { value: 0.82258, label: "Imaginative" },
  { value: 1.0, label: "Unpredictable" }
]

function getTemperatureLabel(value: number): string {
  const match = temperatureStops.find((stop) => stop.value === value)
  return match ? match.label : "Unknown"
}

const toneOptions = [
  { label: "Formal", value: "formal" },
  { label: "Neutral", value: "neutral" },
  { label: "Casual", value: "casual" },
]

const modelOptions = [
    { label: "Mistral", value: "mistralai/Mistral-7B-Instruct-v0.1"},
]

  useEffect(() => {
    const config = { temperature, model, llmRerank, tone }
    fetch("http://localhost:8000/set-config", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(config),
    })
    }, [temperature, model, llmRerank, tone])

  return (
    <div className="absolute top-6 right-6">
      <button
        className={`py-2 px-3 rounded-full flex items-center gap-2 text-neutral-700 dark:text-neutral-300 font-semibold
        ${isOpen 
          ? `bg-neutral-200 dark:bg-neutral-700 hover:bg-neutral-300 dark:hover:bg-neutral-600`
          : `bg-neutral-100 dark:bg-neutral-800 hover:bg-neutral-200 dark:hover:bg-neutral-700`
        }`}
        onClick={() => setIsOpen(!isOpen)}
      >
        <Settings2 size={20} />
        <span className="text-sm">Customize Assistant</span>
      </button>

      {isOpen && (
        <div className="mt-2 p-5 bg-white dark:bg-neutral-950 border border-neutral-100 dark:border-neutral-900 rounded-3xl shadow-lg w-72">
        {/* Tone Selector */}
          <Dropdown
            label="Tone"
            options={toneOptions}
            value={tone}
            onChange={setTone}
          />

            {/* Model Selector */}
            <Dropdown
                label="Model"
                options={modelOptions}
                value={model}
                onChange={setModel}
            />

          {/* Temperature Slider */}
          <div className="mb-7">
            <div className="flex justify-between">
                <label className="block mb-2 text-sm font-medium text-neutral-800 dark:text-neutral-200">
                    Response Style
                </label>
                <p className="block mb-2 text-sm font-medium text-neutral-500 dark:text-neutral-500">{getTemperatureLabel(temperature)}</p>
            </div>
            {/* Background track */}
            <div className="relative w-full h-7 rounded-full bg-neutral-100 dark:bg-neutral-800 hover:bg-neutral-200 hover:dark:bg-neutral-700">
                <div
                className="absolute h-7 rounded-full bg-green-500"
                style={{ width: `${temperature * 100}%` }}
                />

                {/* Transparent slider overlaid */}
                <input
                type="range"
                min="0.1129"
                max="1"
                step="0.17742"
                value={temperature}
                onChange={(e) => setTemperature(parseFloat(e.target.value))}
                className="absolute top-0 left-0 w-full h-3 opacity-0 cursor-pointer"
                />
            </div>
            <style jsx>{`
                input[type='range']::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                width: 0;
                height: 0;
                }
                input[type='range']::-moz-range-thumb {
                width: 0;
                height: 0;
                background: transparent;
                border: none;
                }
            `}</style>
          </div>

          {/* LLM Rerank Toggle (Modern Switch) */}
            <div className="flex justify-between items-center">
            <label className="text-sm font-medium text-neutral-800 dark:text-neutral-200">
                LLM Reranking (Slower)
            </label>
            <button
                onClick={() => setLlmRerank(!llmRerank)}
                className={`w-11 h-7 flex items-center rounded-full p-1
                ${llmRerank ? "bg-green-500 hover:bg-green-600" : "hover:bg-neutral-200 hover:dark:bg-neutral-700 bg-neutral-100 dark:bg-neutral-800"}`}
            >
                <div
                className={`w-6 h-6 bg-white rounded-full
                    ${llmRerank ? "translate-x-3.5" : "translate-x-[-1.5px]"}`}
                />
            </button>
            </div>
        </div>
      )}
    </div>
  )
}
